<template>
    <el-card class="header">
        <div class="le">

            <h2>开始一天的学习吧。</h2>

            <br>
            <p> 重复作业数量： {{ repeat_num }} </p>
            <p> 作业重复率 ： {{ repetition }}</p>
            <p> 加入的课程数量： {{ course_num }}</p>

        </div>
        <div class="ri">
            <img class="ri-img" decoding="async" src="https://demo.gin-vue-admin.com/assets/dashboard.70e55b71.png" alt>
        </div>
    </el-card>


    <el-card>
        <div>
            <div id="main" style="width: 600px;height:300px; float: left;"></div>
            <div id="main2" style="width: 600px;height:300px; float: right;"></div>
        </div>

    </el-card>
</template>

<script>
import { getDashboard } from '@/api/student/api.js'

export default {

    data() {

        return {
            repeat_num: "",
            repetition: "",
            course_num: "",
            // 控制收缩
            isCollapse: false,
        }
    },
    created() {
        this.getDatas()
    },
    methods: {
        async getDatas() {
            const res = await getDashboard()
            if (res.data.code === 200) {
                this.repeat_num = res.data.data.repeat_num;
                this.repetition = res.data.data.repetition;
                this.course_num = res.data.data.course_num;
            }
        },
        leftChart() {
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById("main"));
            // 指定图表的配置项和数据
            let option = {
                title: {
                    text: "课程作业重复率排行"
                },
                tooltip: {},
                legend: {
                    data: ["重复率"]
                },
                xAxis: {
                    data: ["软件1903", "计算机1902", "软件1905", "计算机1901", "计算机1903"]
                },
                yAxis: {},
                series: [
                    {
                        name: "重复率",
                        type: "bar",
                        data: [36, 31, 29, 20, 14, 13]
                    }
                ]
            };
            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
        },
        rightChart() {
            // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById("main2"));
            // 指定图表的配置项和数据
            let option = {
                title: {
                    text: "课程作业数量排行"
                },
                tooltip: {},
                legend: {
                    data: ["数量"]
                },
                xAxis: {
                    data: ["软件1902", "软件1901", "软件1905", "计算机1901", "计算机1903"]
                },
                yAxis: {},
                series: [
                    {
                        name: "数量",
                        type: "bar",
                        data: [20, 15, 13, 12, 11, 10]
                    }
                ]
            };
            // 使用刚指定的配置项和数据显示图表。
            myChart.setOption(option);
        }
    },
    mounted() {
        this.leftChart();
        this.rightChart();

    }
};
</script>
<style>
.el-row {
    margin-bottom: 20px;
}


.box-card {
    height: calc(100vh/4);
}

.card2 {
    height: calc(100vh/3);
}

.le {

    float: left;
}

.ri {
    max-height: 280px;
    float: right;
}

.ri-img {
    height: 600px;
    width: 600px;
    margin-top: -120px;
}

.base {
    width: 100%;
    /* 宽度设置为 100% */
    height: calc(100vh - 16px);
    /* 高度设置为视口高度 */
    /* display: flex;
    justify-content: center;
    align-items: center; */
}

.header {
    margin-bottom: 20px;
}
</style>
  
